@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&family=Roboto&display=swap');

* {
    padding: 0;
    margin: 0;
    font-family: 'Montserrat', serif;
}

body {
    background: #e5e5e5;
}

#root {
    box-sizing: border-box;
    background: white;
    margin: auto;
    border: 1px solid rgba(0, 0, 0, 0.5);
}

.err404 h1, .err404 h3 {
    text-align: center;
    margin-top: 10px;
    margin-bottom: 20px;
}

.custom-container {
    height: auto;
}

.current-rate {
    font-weight: 700;
}

input {
    padding: 5px 0 5px 10px;
    border: 1px solid rgba(0, 0, 0, 0.5);
    margin-bottom: 15px;
    outline: none;
}

.result {
    margin-bottom: 15px;
}

a {
    transition: all .2s linear;
    font-weight: 700;
    background: #18A0FB;
    border-radius: 6px;
    color: white;
    text-decoration: none;
    padding: 10px;
}

a:hover {
    color: white;
    opacity: .5;
}

select {
    padding: 5px;
    outline: none;
    margin-bottom: 15px;
}

ul {
    margin-bottom: 15px;
}

.row {
    margin-bottom: 10px;
    margin-left: 0;
}

ul {
    padding-left: 0;
}

ul li {
    margin-top: 10px;
    margin-bottom: 20px;
    list-style: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

ul li p {
    display: inline-block;
}

ul li p:first-child {
    font-weight: 700;
    margin-right: 15px;
}

.logo {
    margin-bottom: 25px;
}

/* xl (>=1200) */
@media (min-width: 1200px) {
    body {
        padding: 5%;
    }
    .logo{
        margin-top: 10px;
        margin-bottom: 10px;
    }
    #root {
        margin-top: 1%;
        display: block;
        padding: 1%;
        max-width: 45%;
    }

    .logo {
        font-size: 18px;
    }
    .rates-page-link{
        margin-bottom: 20px;
    }
    .rate{
        margin: 3%;
    }
    .err404{
        margin: 3%;
    }
}

